<template>
  <div class="h264video">
    <div class="search-box">
      <a-input
        v-model:value="inputValue"
        placeholder="请输入webrocket地址获取H264视频流"
        size="large"
      />
      <a-button type="primary" @click="play" size="large">播放</a-button>
    </div>
    <div class="video-box">
      <video id="videoPlay" class="video" controls></video>
    </div>
  </div>
</template>
<script setup>
import { ref, onMounted } from "vue";
import JMuxer from "@/assets/js/jmuxer";
let inputValue = ref("");
let jmuxer = ref(null);
const play = () => {
  if (inputValue.value) {
    crateJMuxer();
    createWebcoket(inputValue.value);
  }
};
const crateJMuxer = () => {
  jmuxer = new JMuxer({
    node: "videoPlay",
    mode: "video",
    flushingTime: 1000,
    fps: 30,
    debug: true,
    clearBuffer: false,
  });
};
const createWebcoket = (url) => {
  websocket = new WebSocket(url);
  websokcet.binaryType = "arraybuffer";
  websocket.onbmessage = (ev) => {
    let data = ev;
    jmuxer.feed({
      video: new Uint8Array(data),
    });
  };
};
</script>
<style lang="scss" scoped>
.h264video {
  max-width: 1000px;
  margin: auto;
  padding-top: 50px;
  .search-box {
    display: flex;
    align-items: center;
    justify-content: center;
    .ant-input {
      width: 50%;
      margin-right: 10px;
    }
  }
  .video-box {
    margin-top: 50px;
    .video {
      width: 80%;
      height: auto;
    }
  }
}
</style>